* {
  box-sizing: border-box;
}

body {
  background-color: #deb887;
}

.loader {
  width: 12px;
  height: 12px;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  display: block;
  position: relative;
}

.breed-select {
  color: #fff;
  background-color: #00008b;
  border: 0;
  border-radius: 5px;
  padding: 5px;
  font-size: 18px;
}

.cat-info {
  margin-top: 20px;
  display: flex;
}

.hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-text {
  width: 500px;
  margin-left: 50px;
  font-size: 20px;
}

.cat-img {
  width: 500px;
}

